.productsHeading {
  margin: 2vmax auto;
  width: 15vw;
  border-bottom: 1px solid rgba(0, 0, 0, 0.171);
  padding: 1vmax;
  color: rgba(0, 0, 0, 0.678);
  font: 500 1.9vmax "Roboto";
  text-align: center;
}

.products {
  display: flex;
  flex-wrap: wrap;
  padding: 0 5vmax;
  justify-content: center;
  min-height: 30vh;
}

.paginationBox {
  display: flex;
  justify-content: center;
  margin: 6vmax;
}
.pagination {
  display: flex;
  justify-content: center;
  padding: 0;
}

.page-item {
  background-color: rgb(255, 255, 255);

  transition: all 0.3s;
  cursor: pointer;
}

.page-link {
  text-decoration: none;
  font: 300 1.3vmax "Roboto";
  color: rgb(80, 80, 80);
  transition: all 0.3s;
}

.page-item:hover {
  background-color: rgb(230, 230, 230);
}
.page-item :hover .page-link {
  color: rgb(80, 80, 80);
}

/* .pageItemActive{
    background-color: tomato;
} */

.pageLinkActive {
  color: white;
  background-color: tomato;
}

/* // price slider */

.filterBox {
  width: 10vmax;
  position: absolute;
  top: 10vmax;
  left: 3vmax;
}

/* // category css */
.categoryBox {
  padding: 0%;
}

.category-link {
  list-style: none;
  color: rgba(0, 0, 0, 0.61);
  font: 400 1.2vmax "Roboto";
  margin: 0.4vmax;
  cursor: pointer;
  transition: all;
}

.category-link:hover {
  color: tomato;
}

.filterBox > fieldset {
  border: 1px solid rgba(0, 0, 0, 0.406);
}
